import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router} from "@angular/router";
import {ClientService} from "../../service/client.service";

@Component({
  selector: 'customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.css']
})
export class CustomersComponent implements OnInit {

  valid:Boolean;
  constructor(private _http: HttpClient,private clientService : ClientService, private router:Router) { 
    this.valid = true;
  }

  clientInfo:any[] | any;

  disableEnterKey(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
      return false;
      }
  }


  ngOnInit() {
    this.clientService.fetchClients().subscribe((data)=>{
      this.clientInfo = data.body;
    })
  }

  edit(clientID){
    let clientData: any = {
      queryParams: {
          "ClientID": clientID,
      }
    };
    this.router.navigate(['edit-client'],clientData );
  }

  delete(clientID){
    this.clientService.deleteAppointment(clientID).subscribe((data:any)=>{
      if(data.success){
        location.reload();
      }else{
        console.log('something went wrong');
      }
    });
  };
}


