import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import {ClientService} from "../../service/client.service";
import {HttpClient,  HttpHeaders} from '@angular/common/http';

@Component({
  selector: 'edit-client',
  templateUrl: './edit-client.component.html',
  styleUrls: ['./edit-client.component.css']
})
export class EditClientComponent implements OnInit {

  customerID:any;
  emailPattern:any =/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;


  constructor(private route: ActivatedRoute,private router: Router, private clientService : ClientService) {
    this.route.queryParams.subscribe(params => {
      this.customerID = params["ClientID"];
      });
   }

  contactInfo = {
    email:'',
    name:'',
    bookingDate:null,
    job:''
  };

  hairStore:any = [
    {'hairjob': 'Hair Cut', 'price':25},
    {'hairjob': 'Line Up', 'price':15},
    {'hairjob': 'Beard Clean Up', 'price':15}
  ];

  ngOnInit() {
    this.clientService.retrieveProfile(this.customerID).subscribe((response:any)=>{
      this.contactInfo.email = response.body.email;
      this.contactInfo.name = response.body.name;
      this.contactInfo.bookingDate = response.body.date;
      this.contactInfo.job = response.job['hairjob'];
    })
  }

  updateClient(){
    this.clientService.editAppointment(this.customerID, this.contactInfo).subscribe((data:any)=>{
      console.log('updating data:'+ data);
      if(data.ok){
        //console.log("successfully sent email");
        this.router.navigate(['customers']);
      }else{
        console.log("opps something went wrong");
      }
    });;
  }

  disableEnterKey(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
      //console.log('nice try');
      return false;
    }
  }

}
