import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {HttpClient,  HttpHeaders} from '@angular/common/http';
import { ClientService } from "../../service/client.service";
import * as $ from 'jquery';
import {CookieService} from 'ngx-cookie-service';


@Component({
  selector: 'app-email-contacts',
  templateUrl: './email-contacts.component.html',
  styleUrls: ['./email-contacts.component.css']
})
export class EmailContactsComponent implements OnInit {

  emailPattern:string = "([a-zA-Z0-9]+)([\\_\\.\\-{1}])?([a-zA-Z0-9]+)\\@([a-zA-Z0-9]+)([\\.])([a-zA-Z\\.]+)";
  namePattern:string = "^[a-zA-Z '-]+$";
  data_:any;
  clientData:any;
  edit:boolean = false;
  csrftoken:any;
  hairStore:any = [
    {'hairjob': 'Hair Cut', 'price':25},
    {'hairjob': 'Line Up', 'price':15},
    {'hairjob': 'Beard Clean Up', 'price':15}
  ];

  contactInfo = {
    email:'',
    name:'',
    bookingDate:null,
    job: ''
  };
  valid:boolean = false;
  appointmentBooked:boolean =false;

  constructor(private _http: HttpClient, private router: Router, private cookieService: CookieService,private clientService : ClientService) { }

  ngOnInit() {
    this.loadForm();
    this.csrftoken = this.cookieService.get('XSRF-TOKEN');
  }

  loadForm(){
    $(document).ready(function(){
      $("#emailForm").load(function (){
        $("#emailForm").fadeIn('slow')
      });
    });
  }

  emailUser(){
    //const formattedDate = moment(this.contactInfo.bookingDate).format('LLLL');

    this.data_ = {
      'name': this.contactInfo.name,
      'email':this.contactInfo.email,
      'date': this.contactInfo.bookingDate,
      'job': this.contactInfo.job
    };

    this.clientData = JSON.stringify(this.data_);
    
    this.clientService.saveClient(this.data_).subscribe((data)=>{
      console.log('saving data:'+ data);
    });

    this.clientService.sendClientData(this.data_).subscribe((data:any)=>{
      console.log(this.data_);
      if(data){
        this.appointmentBooked = data.success;
        console.log("successfully sent email");
        this.router.navigate(['success',{'client': this.clientData}]);
      }else{
        console.log("opps something went wrong");
      }
    })
  }

  disableEnterKey(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
      return false;
      }
  }

  disableShiftKey(e){
    if(e.shiftKey && e.which === 83){
      event.preventDefault();
      alert('invalid entry');
      return false;
    }else{
      //alert('it works');
      console.log('testing');
    }

  }

  toggle(){
    this.edit = !this.edit;
  }

  onSuccess(){
    this.valid = this.clientService.validateUser();
  }


}
