import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
import { CookieService } from 'ngx-cookie-service';


@Injectable()
export class ClientService {
  authToken:any;

  constructor(private _http: HttpClient,private cookieService: CookieService) { }

  HEADERS = new HttpHeaders({
    'Content-Type': 'application/json',
    'X-XSRF-TOKEN':this.cookieService.get('XSRF-TOKEN')
  });

  fetchClients() {
    return this._http.get("/client",{headers:this.HEADERS,observe:'response'}).map((response) =>{return response} );
  }

  retrieveProfile(id){
    return this._http.get("/client/"+id,{headers:this.HEADERS,observe:'response'}).map((response) =>{return response} );
  }
  
  editAppointment(id:any, data:any){
    return this._http.put("/client/"+id,data,{headers:this.HEADERS,observe:'response'});
  }

  deleteAppointment(id:any){
    return this._http.delete("/client/"+id);
  }

  saveClient(data:any){
    return this._http.post("/client/save",data,{headers:this.HEADERS, observe:'response'});
  }

  sendClientData(data:any){
    return this._http.post("/client/sendmail",data,{headers:this.HEADERS,observe:'response'});
  }

  registerBarber(data:any){
    return this._http.post("/client/register",data,{headers:this.HEADERS,observe:'response'});
  }

  login(data:any,options?:any){
    return this._http.post("/client/authenticate",data,{headers:this.HEADERS,observe:'response'});
  }

  fetchToken(){
    return this._http.get("/client/token")
  }


  storeUserData(token) {
    localStorage.setItem('jwt_token', token);
  }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  validateUser(){
    return tokenNotExpired('jwt_token');
  }

  logoutUser(){
    localStorage.clear();
  }

}

