import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {HttpClient,  HttpHeaders} from '@angular/common/http';
import { ClientService } from "../../service/client.service";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {


  constructor(private _http: HttpClient, private router: Router,private clientService : ClientService) { }

  data:any;

  barberInfo = {
    username:'',
    password:'',
    email: ''
  };

  ngOnInit() {
  }

  disableEnterKey(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
      return false;
    }
  }

  register(){
    this.data = {
      'username': this.barberInfo.username,
      'email':this.barberInfo.email,
      'password':this.barberInfo.password
    };
    
    this.clientService.registerBarber(this.data).subscribe((data)=>{
      console.log('registering data:'+ data);
      if(data){
        console.log('success');
      }else{
        console.log('fail');
      }
    });
  }

}
