import { Component, OnInit,  } from '@angular/core';
import {Router} from "@angular/router";
import {HttpClient,  HttpHeaders} from '@angular/common/http';
import { ClientService } from "../../service/client.service";
import {CookieService} from 'ngx-cookie-service';

@Component({
  selector: 'app-login-app',
  templateUrl: './login-app.component.html',
  styleUrls: ['./login-app.component.css']
})
export class LoginAppComponent implements OnInit {

  constructor(private _http: HttpClient, private router: Router,private clientService : ClientService,private cookieService: CookieService) { }

  data_:any;
  alertMessage:String;
  showAlert:Boolean;
  emailPattern:string = "([a-zA-Z0-9]+)([\\_\\.\\-{1}])?([a-zA-Z0-9]+)\\@([a-zA-Z0-9]+)([\\.])([a-zA-Z\\.]+)";
  tokenSet:boolean=false;
  barberInfo = {
    password:'',
    email: ''
  };
  loggedIn:boolean=true;

  ngOnInit(){
    this.showAlert = true;
  }

  login(){
    let HEADERS = new HttpHeaders();
    HEADERS.set('Content-Type', 'application/x-www-form-urlencoded');
    /*let HEADERS = new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded',
      'X-XSRF-TOKEN':this.cookieService.get('XSRF-TOKEN')
    });*/
      this.data_ = {
        'email':this.barberInfo.email,
        'password':this.barberInfo.password
      };
    

    this.clientService.login(this.data_).subscribe((data:any)=>{
      console.log(this.data_);
      if(data.body.token){
        this.clientService.storeUserData(data.body.token);
        console.log("successfully sent email");
        this.router.navigate(['home']);
      }else{
        this.showAlert = data.body.success;
        this.alertMessage = 'Invalid username or password';
        console.log("opps something went wrong");
      }
    })
  }

  signIn(){
    this.clientService.fetchToken().subscribe();
    if(this.cookieService.get('XSRF-TOKEN') !== undefined){
      console.log(this.cookieService.get('XSRF-TOKEN'));
      this.tokenSet=true;
    }else {
      this.loggedIn=false;
    }
  }

  disableEnterKey(event) {
    if (event.keyCode === 13) {
        event.preventDefault();
      return false;
    }
  }

}
